<template>
  <div class="container">
    <div class="deve-title">
      <div class="title-en">PROJECTS</div>
      <div class="title-cn"></div>
    </div>
    <v-touch class="deve-list"
      id="deve-list"
      v-on:swipeleft="swiperleft"
      v-on:swiperight="swiperright">
      <div class="row">
        <div class="deve-item" :style="{backgroundImage:item.url}" v-for="(item,index) in deveData" :key="index">
          <div class="deve-item-title">{{item.title}}</div>
          <div class="deve-item-desc">{{item.desc}}</div>
        </div>
      </div>
      <div class="row">
        <div class="deve-item" :style="{backgroundImage:item.url}" v-for="(item,index) in deveDatas" :key="index">
          <div class="deve-item-title">{{item.title}}</div>
          <div class="deve-item-desc">{{item.desc}}</div>
        </div>
      </div>
    </v-touch>
    <div class="deve-line">
      <div class="deve-line-index" id="deve-line-index" :style="{width:100/itemMax+'%'}"></div>
    </div>
    <div class="deve-more">
      <div class="deve-more-left">
        <p class="deve-more-en">SLIDE FOR MORE</p>
        <p class="deve-more-cn"></p>
      </div>
      <div class="deve-more-right">
        <div class="deve-more-ball"></div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data(){
    return{
      deveData:[
        {title:'',desc:'CAMILLIA RISE',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
        {title:'',desc:'HOUSE OF BEGONIA',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'THE GLADIOLUS',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
        {title:'',desc:'ROSE INTERNATIONAL',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'ORCHID SHORE',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/ORCHID-SHORE-Intro-2.jpg)'},
        {title:'',desc:'ROSE MANOR',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'LAKEVIEW COMMUNITY',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'WISTERIA LAKE',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
      ],
      deveDatas:[
        {title:'',desc:'QIANGWEI HUAYUAN',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
        {title:'',desc:'BOSTON IVY',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/-1.jpg)'},
        {title:'',desc:'ROSA INTERNATIONAL',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/08/3e658b0fa85713ed7a6d47caeb276c6.jpg)'},
        {title:'',desc:'ROYAL IVY',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
        {title:'',desc:'RIVER ROSELLE',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
        {title:'',desc:'THE WISTERIA',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'BEGONIA WEAVE',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2019/06/.jpg)'},
        {title:'',desc:'CRYSTAL BAY',url:'url(http://www.seedland.cc/wp/wp-content/uploads/2018/10/.jpg)'},
      ],
      itemWidth:null,
      translateX:0,
      itemMax:6,    //-2
      lineTranslateX:0
    }
  },
  methods:{
    swiperleft(){
      let list = document.getElementById('deve-list');
      let width = list.offsetWidth/2
      this.itemWidth = width * this.itemMax  //
      
      if(this.translateX<this.itemWidth){
        this.translateX = this.translateX +width
        list.style.transform = `translateX(-${this.translateX}px)`
      }

      let line = document.getElementById('deve-line-index');
      if(this.lineTranslateX<100*(this.itemMax-1)){
        this.lineTranslateX = this.lineTranslateX + 100
        line.style.transform = `translateX(${this.lineTranslateX}%)`
      }
    },
    swiperright(){
      let list = document.getElementById('deve-list');
      let width = list.offsetWidth/2;
      this.itemWidth = width * this.itemMax   

      if(this.translateX>0){
        this.translateX = this.translateX -width
        list.style.transform = `translateX(-${this.translateX}px)`
      }

      let line = document.getElementById('deve-line-index');
      if(this.lineTranslateX>0){
        this.lineTranslateX = this.lineTranslateX - 100
        line.style.transform = `translateX(${this.lineTranslateX}%)`
      }
    }
  }
}
</script>

<style lang="less" scoped>
@divide: 10;
@pswWidth: 640;
@p: @divide/@pswWidth * 1rem;

.container{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  color: white;
  .deve-title{
    margin: 15% 5% 0 0;
    text-align: end;
    .title-en{
      font-size: 22*@p;
      font-weight: 300;
      letter-spacing: 3*@p;
      line-height: 1.5;
    }
    .title-cn{
      font-size: 24*@p;
    }
  }
  .deve-list{
    width: 100%;
    height: 50%;
    margin-top: 20*@p;
    transition: transform .8s;
    .row{
      width: 100%;
      height: 50%;
      white-space: nowrap;
      display: flex;
      .deve-item{
        width: 50%;
        flex: none;
        position: relative;
        height: 100%;
        vertical-align: top;
        background-size: cover;
        border: 3*@p solid black;
        border-left: 1*@p solid black;
        border-right: 1*@p solid black;
        .deve-item-title{
          font-size: 26*@p;
          padding: 15*@p 0 0 15*@p;
        }
        .deve-item-desc{
          font-size: 28*@p;
          position: absolute;
          left: 15*@p;
          bottom: 15*@p;
        }
      }
    }
  }
  .deve-line{
    height: 15*@p;
    background-color: rgba(51, 51, 51, 0.322);
    .deve-line-index{
      background-color: #ffffff9f;
      border-radius: 15*@p;
      height: 15*@p;
    }
  }
  .deve-more{
    display: flex;
    margin: 20*@p;
    .deve-more-left{
      font-size: 22*@p;
      line-height: 1.5;
      text-align: end;
      margin-right: 20*@p;
      .deve-more-cn{
        font-size: 18*@p;
      }
    }
    .deve-more-right{
      border: 4*@p solid #fff;
      height: 46*@p;
      width: 75*@p;
      border-radius: 25*@p;
      padding: 7*@p;
      display: block;
      position: relative;
      margin-top: 13*@p;
      .deve-more-ball{
        position: absolute;
        border: 2*@p solid #fff;
        background-color: #fff;
        height: 15*@p;
        width: 15*@p;
        border-radius: 20*@p;
        top: 26%;
        animation: slideiconleft 2s 1s linear infinite;
      }
      @keyframes slideiconleft {
        0%{
          opacity: 1;
          left:70%;
        }
        100%{
          opacity: 0;
          left: 10%;
        }
      }
    }
  }
}
</style>